.intro {
    position: relative;
    padding-top: 4em;
}

.intro__img {
    box-shadow: var(--bs);
    border-radius: 2%;
}

.section__subtitle--intro {
    display: inline-block;
}

@media (min-width: 700px) {
    .intro {
        display: grid;
        width: min-content;
        margin: 0 auto;
        column-gap: 1em;
        grid-template-areas: 
        "img title"
        "img subtitle";
        grid-template-columns: min-content max-content;
    }

    .intro__img {
        grid-area: img;
        min-width: 250px;
        position: relative;
        z-index: 2;
    }

    .section__subtitle--intro {
        align-self: start;
        grid-column: -1 / 1;
        grid-row: 2;
        text-align: right;
        position: relative;
        left: -1.5em;
        width: calc(100% + 1.5em);
    }

    .section__subtitle--intro strong {
        display: block;
    }
}